<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4>
        <v-card class="elevation-12">
          <v-alert shaped type="error" v-if="error">
            {{ error }}
          </v-alert>
          <v-toolbar dark color="primary">
            <v-toolbar-title>Login form</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-select
                v-model="login.user"
                prepend-icon="mdi-account"
                :items="users"
                label="Utilisateur"
              ></v-select>
              <v-text-field
                v-model="login.password"
                prepend-icon="mdi-lock"
                label="Password"
                type="password"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="connect()">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import dataLogin from "../data/users.json";
export default {
  methods: {
    connect() {
      this.error = "";
      const user = dataLogin.find((element) => element.user == this.login.user);
      if (!user) {
        return (this.error = "utilisateur non identifié");
      }

      if (user.password != this.login.password) {
        return (this.error = "mot de passe erroné");
      }
      this.$store.state.login = true;
      this.$store.state.user = this.login.user;
      this.login = {
        user: "",
        password: "",
      };
      this.$router.push({ name: "Caisse" });
    },
  },
  data: () => ({
    users: [
      "Marcus",
      "Vincent",
      "Donna",
      "Felix",
      "Davina",
      "Marc",
      "Alexandre",
    ],
    login: {
      user: "",
      password: "",
    },
    error: "",
  }),
};
</script>